"use client";

import { useEffect, useState } from "react";
import Header from "../components/Header";
import Footer from "../components/Footer";
import HeroSlider from "../components/HeroSlider";
import { getPublishedContents } from "@/services/content.service";


import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/autoplay";

import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Autoplay } from "swiper/modules";

export default function Home() {
  const [contents, setContents] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    getPublishedContents()
      .then((res) => setContents(res.data))
      .finally(() => setLoading(false));
  }, []);

  if (loading) return <p className="text-center">جاري التحميل...</p>;

  return (
    <div className="min-h-screen bg-black text-white">
      <Header />

      {/* مثال: السلايدر من المسلسلات */}
      <HeroSlider
        movies={contents.filter((c) => c.type === "series")}
      />

      <div className="px-4 md:px-8 lg:px-16 py-12 space-y-16">
        {renderSection("المسلسلات", "/series", contents, "series")}
        {renderSection("الأفلام", "/movies", contents, "movie")}
        {renderSection("البرامج", "/programs", contents, "program")}
        {renderSection("البودكاست", "/podcasts", contents, "podcast")}
        {renderSection("المسرحيات", "/plays", contents, "play")}
        {renderSection("الأطفال", "/kids", contents, "kids")}
      </div>

      <Footer />
    </div>
  );
}
function renderSection(
  title: string,
  link: string,
  allData: any[],
  type: string
) {
  const data = allData.filter((item) => item.type === type);

  if (data.length === 0) return null;

  return (
    <section>
      <div className="flex items-center mb-8 justify-between">
        <h2 className="text-2xl md:text-3xl font-bold">{title}</h2>
        <a href={link} className="text-red-500">عرض الكل</a>
      </div>

      <Swiper /* نفس إعداداتك */>
        {data.map((item) => (
          <SwiperSlide key={item.uuid}>
            <MovieCard
              movie={{
                id: item.uuid,
                title: item.title,
                cardimg: `http://localhost:8000${item.card_image}`,
                rating: item.rating ?? 0,
                year: item.year ?? 0,
              }}
              type={type}
            />
          </SwiperSlide>
        ))}
      </Swiper>
    </section>
  );
}
